.Clinic {
  font-size: 12px;
  color: #222222;
  line-height: 16px;
}

.Location {
  flex-direction: row;
  display: flex;
  justify-content: space-between;
  border-bottom: 0.5pt solid #ced1db;
  padding-bottom: 8px;
}

.Clinic svg {
  flex-shrink: 0;
}

.Stations {
  flex-direction: row;
  display: flex;
  flex-wrap: wrap;
}

.OrderDescription {
  color: var(--text_secondary);
  text-align: center;
}

.Address {
  margin-bottom: 4px;
}

.StationDistance {
  margin-bottom: 4px;
}

.Order {
  padding-top: 16px;
}

.OrderButton {
  margin-bottom: 8px;
}
