.RatingStars {
  position: relative;
  height: 14px;
}

.Full svg path {
  fill: #ffc611;
}

.Empty svg path {
  fill: #ced1db;
}

.RatingStars svg {
  vertical-align: top;
  margin-right: 2px;
}

.RatingStars svg:last-child {
  margin-right: 0px;
}

.Full {
  position: absolute;
  white-space: nowrap;
  overflow-x: hidden;
  overflow-y: hidden;
  top: 0;
}
