.Doctor {
  display: flex;
  flex-direction: row;
}

.Avatar {
  border-radius: 4px;
  height: 74px;
  width: 74px;
  background-position: top;
  background-size: cover;
  margin-right: 12px;
  flex-shrink: 0;
}

.Info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
}

.Price {
  line-height: 22px;
  font-size: 17px;
  font-weight: 500;
}

.Pricing span {
  margin-right: 8px;
}

.OldPrice {
  font-size: 12px;
  color: #222222;
  text-decoration-line: line-through;
  opacity: 0.5;
}

.Speciality {
  font-size: 12px;
  color: var(--text_secondary);
}

.Rating {
  display: flex;
  flex-direction: row;
}
